<template>
  <v-container>
    <v-carousel 
      hide-delimiters 
      v-if="Object.keys(this.getRecentEvents).length > 0"
      >
      <v-carousel-item
        v-for="getEvent in getRecentEvents"
        :key="getEvent.id"
        :src="'/images/' + getEvent.image"
        cycle
        @click="$router.push({ path: `/event/${getEvent.id}` })"
      >
      <v-card class="transparent">
        <v-card-title style="color: black; opacity: 1; z-index:1">{{ getEvent.title }}</v-card-title>
        <v-card-text style="color: black; opacity: 1; z-index:1"><v-icon style="color:black; opacity: 1">mdi-google-maps</v-icon>Технополис "ЭРА" </v-card-text>
        <v-card-text style="color: black; opacity: 1; z-index:1"><v-icon style="color:black; opacity: 1">mdi-clock</v-icon>{{ new Date(getEvent.timeStart).toLocaleDateString() }} - {{ new Date(getEvent.timeStop).toLocaleDateString() }}</v-card-text>
        </v-card>
        </v-carousel-item>
    </v-carousel>
  <v-row style="margin-top: 20px;">
    <v-col cols="12" sm="6" md="4">
        <v-img
          class="white--text align-end"
          height="200px"
          src='https://www.era-tehnopolis.ru/upload/iblock/af5/333.jpg'
        >
          <v-card-title>Программа</v-card-title>
        </v-img>

        <v-card-text class="text--primary">
          <div>В то время некий безымянный печатник создал большую коллекцию размеров и форм шрифтов, используя Lorem Ipsum для распечатки образцов. Lorem Ipsum не только успешно пережил без заметных изменений пять веков, но и перешагнул в электронный дизайн. Его популяризации в новое время послужили публикация листов Letraset с образцами Lorem Ipsum в 60-х годах и, в более недавнее время, программы электронной вёрстки типа Aldus PageMaker, в шаблонах которых используется Lorem Ipsum.
    Почему он используется?</div>
        </v-card-text>

        <v-card-actions>
          <router-link to="/events"><v-btn
            color="purple darken-4"
            text
          >
            Подробнее
          </v-btn></router-link>
        </v-card-actions>
    </v-col>
  </v-row>
  </v-container>
</template>

<script>

import { mapGetters } from 'vuex'
  export default {
    data () {
      return {
      }
    },

    async mounted() {

      console.log(this.getUser)
      if (!this.getUser || Object.keys(this.getUser).length == 0) {
        this.$fire({
            title: "Уведомление",
            text: "Необходимо авторизоваться!",
            type: "error",
        })
        .then(this.$router.push('/howtouse'));
      }
      
      await this.getBlue()
      document.title = 'Технополис "ЭРА" | Главная';
      this.$emit('update:title', 'Главная')
      await this.$store.dispatch('getRecentEvents')
    },

    computed: {
       ...mapGetters(['getRecentEvents', 'getUser']),
    },

    methods: {
      getBlue() {
          for (let i = 0; i < this.getRecentEvents.length; i++) {
              if (Number(new Date(this.getRecentEvents[i].timeStart)) < Date.now() && Number(new Date(this.getRecentEvents[i].timeStop)) > Date.now()) {
                  this.getRecentEvents[i].isActive = 1
              }
              else if (Number(new Date(this.getRecentEvents[i].timeStop)) < Date.now()) {
                this.getRecentEvents[i].isActive = 2;
              }
              else if (Number(new Date(this.getRecentEvents[i].timeStart)) > Date.now()) {
                this.getRecentEvents[i].isActive = 0;
              }
          }
      },

      
    }
  }
</script>

<style scoped>
  .text--primary {height: 150px; overflow: hidden;}

  .transparent {
   /* position: relative; */
   position:absolute;
   width: 100%;
   bottom: 0;
   right:0;
   background-color: white!important;
   opacity: 0.7;
   border-color: transparent!important;
   border-radius: 0px !important;
 }

 .chip {
   width: 100%;
 }

</style>