import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);
import axios from 'axios'

export default new Vuex.Store({
    state: {
        user: JSON.parse(localStorage.getItem('user') || '[]'),
        guests: [],
        users: [],
        recEvents: [],
        getUrl: 'http://10.0.0.109:3000/',
        place: ''
    },
    mutations: {
        add_user(state, data) {
            state.user = data
        },
        getGuests(state, data) {
            state.guests = data
        },
        getUsers(state, data) {
            state.users = data
        },
        getRecentEvents(state, events) {
            state.recEvents = events
        },

        getPlace(state, place) {
            state.place = place
        }
    },
    actions: {

        async getUsers({commit}) {
            return new Promise((resolve, reject) => {
                axios.get(this.state.getUrl + 'api/getUsers')
                    .then(resp => {
                        if (resp.data) {
                            console.log("Success!", resp.data)
                            commit('getUsers', resp.data)
                            resolve(resp)
                        }
                    })
                    .catch(err => {
                        reject(err)
                    })
            })
        },

        async login({ commit }, id) {
            console.log(id)
            return new Promise((resolve, reject) => {
                axios({ url: this.state.getUrl + 'api/auth/' + id, method: 'GET' })
                    .then(resp => {

                        if (resp.data) {
                            console.log("Success!", resp.data)
                            resp.data.isLoggedIn = true
                            localStorage.setItem('user', JSON.stringify(resp.data))
                            commit('add_user', resp.data)
                            resolve(resp)
                        }
                    })
                    .catch(err => {
                        localStorage.removeItem('user')
                        reject(err)
                    })
            })
        },

        async getGuests({ commit }, id) {
            return new Promise((resolve, reject) => {
                axios.get(this.state.getUrl + 'api/guests/' + id)
                    .then(resp => {
                        if (resp.data) {
                            commit('getGuests', resp.data)
                            resolve(resp)
                        }
                    })
                    .catch(err => {
                        reject(err)
                    })
            })
        },

        async updateUser(id) {
            axios({ url: this.state.getUrl + 'api/setLog/' + id, method: 'GET' })
                .then(resp => {
                    if (resp.data) {
                        console.log("Success!", resp.data)
                    }
                })
        },

        async getRecentEvents({ commit }) {
            return new Promise((resolve, reject) => {
                axios.get(this.state.getUrl + 'api/recentEvents')
                    .then(res => {
                        commit('getRecentEvents', res.data)
                        resolve(res.data)
                    })
                    .catch(err => {
                        reject(err)
                    })
            })
        },

        async createEvent(data) {
            console.log(data)
            await axios.post(this.state.getUrl + 'api/createEvent', { data: data })
                .then(res => {
                    console.log(res)
                })
        }
    },

    getters: {

        getUser: state => {
            return state.user
        },

        guestsGetter: state => {

            var guests_arr = []

            state.guests.forEach(elem => {
                console.log(elem[0])
                if (elem[0].isLoggedIn == true) {
                    elem[0].isLoggedIn = "Авторизован"
                    elem[0].color = true
                } else {
                    elem[0].isLoggedIn = "Не авторизован"
                    elem[0].color = false
                }
                guests_arr.push(elem[0])
            })
            return guests_arr
        },

        getRecentEvents: state => {
            var array = []
            state.recEvents.forEach(elem => {
                if (new Date(elem.timeStop) >= Date.now()
                    // && new Date(elem.timeStart) < Date.now()
                ) {
                    array.push(elem)
                }
            })
            return array
        },

        getEvents: state => {
            var array = []

            state.recEvents.forEach(elem => {
                if (new Date(elem.timeStop) >= Date.now() && new Date(elem.timeStart) < Date.now()) {
                    elem.isActive = 1
                }
                else if (new Date(elem.timeStop) < Date.now()) {
                    elem.isActive = 0
                }
                else if (new Date(elem.timeStart) >= Date.now()) {
                    elem.isActive = 2
                }
                array.push(elem)
            })

            return array
        },

        isAdmin: state => {
            if (state.user.isActive == "0") {
                console.log("ADMIN")
                return true
            }
            else {
                return false
            }
        },

        getUrl: state => state.url,

        getUsers: state => state.users,

        placeGetter: state => state.place
    },

    modules: {}
});